import { __decorate } from "tslib";
import { Component, Vue } from "vue-property-decorator";
const carousel = require('vue-owl-carousel');
let Partner = class Partner extends Vue {
};
Partner = __decorate([
    Component({
        components: {
            carousel
        }
    })
], Partner);
export default Partner;
